<template>
    <div class='c-box'>
        <Row class='c-row'>
           <i-col class-name='mysorch' :xs="{span:24}" :sm="{span:24}" :lg="{span:24}">
                <RigthView :count='true'/>
            </i-col>
            <i-col span='24' :xs="{span:24}" :sm="{span:24}" :lg="{span:24}">
                <div class='class-box'>
                    <Anchor show-ink>
                        <AnchorLink href="#basic_usage" title="个人简介" />
                        <AnchorLink href="#static_position" title="个人优势" />
                        <AnchorLink href="#API" title="工作经历"/>
                        <AnchorLink href="#basic_usage" title="项目介绍" />
                        <AnchorLink href="#static_position" title="技术概况" />
                    </Anchor>
                </div>
            </i-col>

             <!-- <i-col class-name='brief' :xs="{span:0}" :sm="{span:8,push:16}">
                <RigthView/>
             </i-col>  -->
        </Row>
    </div>
</template>

<script>
import RigthView from '../plugins/rightView'
export default {
    data(){
        return {
           MapArray:[]
        }
    },
    created(){
        this.getDatas()
    },
    methods:{
        async getDatas(){
            let res = await this.axios.get('/server/getlebal');
            if(res){
                this.MapArray = res.data.result
            }
        },
        setlist(name){
            this.$router.push({
                name:'classlist',
                params:{
                    leb:name
                }
            })
        }
    },
    components:{
        RigthView,
    }
}
</script>

<style lang="stylus" scoped>
    .class-box
        width 100%
        background white
        border-radius 10px
        margin-top 20px
        padding 20px
    .c-box
        width 100%
        height 100%
        display flex
        justify-content center
        .c-row
            width 95%
            height auto 
            display flex
            flex-direction column
        .mysorch
            margin-top 10px
            border-radius 10px

</style>





